.App {
    background: -webkit-linear-gradient(#EB5757, #D59152);
    background: -o-linear-gradient(#EB5757, #D59152);
    background: -moz-linear-gradient(#EB5757, #D59152);
    background: linear-gradient(#EB5757, #D59152);
  min-height: 800px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
